<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->
      </el-header>
      <!-- 显示表格 start -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="表格" name="table">
          <el-main>
            <!-- 表格 start -->
            <el-table
              class="table"
              :data="tableData"
              style="width: 100%"
              border
              v-loading="tableLoading"
              :header-cell-style="{
                background: '#f2f2f2',
                color: '#606266',
                height: '38px',
                padding: '0px'
              }"
              :cell-style="{ height: '38px', padding: '0' }"
              :summary-method="getSummaries"
              show-summary
            >
              <el-table-column
                v-for="(value, key) in tableFields"
                :key="key"
                :prop="value"
                :label="key"
                align="center"
                min-width="130"
              >
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <!-- 表格 end -->

            <!-- 导出的表格 start -->
            <!-- 此表格始终保持隐藏 -->
            <el-table
              id="outTable"
              class="table"
              :data="exportTableData"
              style="width: 100%; display: none"
              border
            >
              <el-table-column
                v-for="(value, key) in tableFields"
                :key="key"
                :prop="value"
                :label="key"
                min-width="130"
                align="center"
              >
              </el-table-column>
            </el-table>
            <!-- 导出的表格 end -->
          </el-main>
          <el-footer>
            <!-- 分页 start -->
            <div class="block">
              <el-pagination
                background
                layout="prev, pager, next, total, sizes, jumper"
                :total="total"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                @current-change="onCurrentChange"
                @size-change="onSizeChange"
              >
              </el-pagination>
            </div>
            <!-- 分页 end -->
          </el-footer>
        </el-tab-pane>
        <el-tab-pane label="图表" name="echarts">
          <div class="my-echarts">
            <div id="myChart"></div>
          </div>
        </el-tab-pane>
      </el-tabs>

