          label: '库存'
        },
        {
          value: '30',
          label: '运营'
        },
        {
          value: '40',
          label: '报废'
        }
      ], // 搜索表单设备状态方式选项(在产、库存、运营、报废)

      equipmentStatusOptions: [
        {
          value: '10',
          label: '在产'
        },
        {
          value: '20',
