        },
        {
          value: '30',
          label: '运营'
        },
        {
          value: '40',
          label: '报废'
        }
      ], // 提交表单设备状态方式选项(在产、库存、运营、报废)

      // 当日运行时长、当月运行时长、当年运行时长、累计运行时长

      // dateStateOptions: [
      //   {
      //     value: '1',
