     * 初始化表格数据
     */
    async initTableData() {
      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/operateSaturation/getOperateSaturationList',
        {
          params: {
            ...this.searchValidateForm,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.tableData = res.data
      this.total = res.total
      var type = JSON.parse(res.type)
      this.sumTotal = type.sum
      this.nowsumTotal = type.nowsum
      this.saturationTotal = type.saturation

      this.tableLoading = false

      // var resultData = res.data.concat()
      // resultData.forEach((item, index) => {
      //   if (this.searchVF.type == '2' && item.订单状态 == '实际收入金额') {
