     */
    async delTableData(softAssetInformationId) {
      console.log(softAssetInformationId)
      const { data: res } = await this.$http.delete(
        '/system/robotSoftAssetInformation/delRobotSoftAssetInformation',
        {
          params: {
            softAssetInformationId
          }
        }
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/uploadExcelRobotSoftAssetInformation',
        {},
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.click()
      this.downloadLoading = false
    },

    /**
     * 获取机器人id列表
     */
    async getRobotIdList() {
      const { data: res } = await this.$http.get('/system/robot/getRobotIdList')
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }
      this.robotOptions = res.data
    },

    /**
     * 获取机器人运行饱和度折线图数据
     */
    async getOperateSaturationLineChart() {
      const { data: res } = await this.$http.get(
        '/system/operateSaturation/getOperateSaturationLineChart',
        {
          params: {
            ...this.searchValidateForm
          }
        }
      )

      this.xAxisData = res.list
      this.saturationData = res.data.map((item) => {
        return (item * 100).toFixed(2)
      })

      this.drawEcharts()
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SCENIC_SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      // this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.ascriptionCompanyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.ascriptionCompanyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.ascriptionCompanyId =
          searchCompanyOptions[0].COMPANY_ID
      }
    },

