      })
    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL()
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
          this.getOperateSaturationLineChart()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 接口导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 文件上传之前执行
    handleBeforeUpload(file) {
      this.importLoading = true
    },

    // 文件上传成功后执行
    handleImportSuccess() {
      this.importLoading = false
      this.$message({
        type: 'success',
        message: '已成功导入Excel表格！',
        offset: 100,
        center: true,
        duration: 1000
      })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增机器人资产信息'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row)
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象（防止改变表单数据时响应表格该行数据）

      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑机器人资产信息'
    },

    // 点击删除时触发
    delRow(index, row) {
      var softAssetInformationId = row.softAssetInformationId
      this.$confirm('确认删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.delTableData(softAssetInformationId)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }

          this.$refs.validateForm.resetFields() // 重置表单（清空无效）

          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 搜索表单设备状态改变触发
    onChangeSearchEquipmentStatus(type) {},

    // 提交表单设备状态改变触发
    onChangeEquipmentStatus(type) {
      console.log(type)
    },

    // 提交表单机器人编号改变触发
    onChangeRobotId(robotId) {
      console.log(robotId)
    },

    // 提交表单公司改变触发
    onChangeCompany(companyId) {
      console.log(companyId)
    },

    // 合计行方法
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '统计'
          return
        } else if (index === 1) {
          sums[index] = this.sumTotal || 0
        } else if (index === 2) {
          sums[index] = this.nowsumTotal || 0
        } else if (index === 3) {
          sums[index] = `${(this.saturationTotal &&
            (this.saturationTotal * 100).toFixed(2)) ||
            0}%`
        }
      })
      return sums
    },

    // 切换 tab 时触发
    handleTabClick(tab, event) {
      console.log(tab, event)
    },

    /**
     * 画Echarts图表
     */
    drawEcharts() {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['机器人运营饱和度'],
          top: 0 // 把legend放到柱状图上面
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: {
          name: '机器人运行饱和度（%）',
          type: 'value',
          axisLabel: {
            formatter: '{value}%'
          }
        },
        series: [
          {
            name: '机器人运营饱和度',
            data: this.saturationData,
            type: 'line',
            tooltip: {
              valueFormatter: function(value) {
                return value + ' %'
              }
            }
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    // activeFixed() {
    //   return (value) => {
    //     // 冻结左侧“合作主体、景区名称、机器人编号”
    //     if (value == 'SCENIC_SPOT_NAME' || value == 'robotCode') {
    //       return 'left'
    //     }
    //   }
    // }

    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'saturation') {
          result = `${(row[value] * 100).toFixed(2)}%`
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

